*{margin:0;padding:0;box-sizing:border-box}.navbar{background-color:#fff}.navbar-brand{color:#565387!important;font-size:1.9rem;font-weight:600}.navbar a{font-size:1.2rem;text-transform:capitalize}.navbar a:hover{color:#3498db!important}.menu-active{font-weight:700;border-bottom:1px solid #565387}#header{width:100%;height:73vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:#000}#header .brand-name{color:#3498db}#header h2{color:#484848;font-size:24px}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1;display:inline-block;padding:10px 30px;border-radius:50px;transition:.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background:#3498db;color:#fff}.header-img{text-align:right}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{transform:translateY(10px)}100%{transform:translateY(-10px)}}@keyframes up-down{0%{transform:translateY(10px)}100%{transform:translateY(-10px)}}